'use client'
import React, { useState } from "react";
import { FaSearch, FaShoppingCart, FaBars, FaTimes } from "react-icons/fa";
import { motion, AnimatePresence } from "framer-motion";
import UseCartContext from "@/context/CartContext";
import LoginPage from "@/app/login/page";

const Navbar = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [searchOpen, setSearchOpen] = useState(false);

  const { cartItems } = UseCartContext();

  return (
    <motion.header
      className="w-full bg-black text-gray-200 shadow-md sticky top-0 z-50"
      initial={{ y: -80, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.6 }}
    >
      <div className="max-w-7xl mx-auto flex items-center justify-between px-6 py-4">
        {/* Logo */}
        <div className="text-2xl font-bold text-blue-400 tracking-wide">
          E-Market Place
        </div>

        {/* Center Menu */}
        <div className="hidden md:flex flex-1 justify-center gap-8">
          <a href="#" className="hover:text-blue-400 transition">Home</a>
          <a href="#" className="hover:text-blue-400 transition">Products</a>
          <a href="#" className="hover:text-blue-400 transition">Categories</a>
          <a href="#" className="hover:text-blue-400 transition">About</a>
          <a href="#" className="hover:text-blue-400 transition">Contact</a>
        </div>

        {/* Right Icons */}
        <div className="flex items-center gap-4 md:gap-6">
          {/* Search Icon */}
          <div className="relative">
            <FaSearch
              className="text-gray-200 hover:text-blue-400 transition text-xl cursor-pointer"
              onClick={() => setSearchOpen(!searchOpen)}
            />
            {/* Animated Search Bar */}
            <AnimatePresence>
              {searchOpen && (
                <motion.div
                  initial={{ width: 0, opacity: 0 }}
                  animate={{ width: 200, opacity: 1 }}
                  exit={{ width: 0, opacity: 0 }}
                  className="absolute right-0 top-0 flex items-center overflow-hidden"
                >
                  <input
                    type="text"
                    placeholder="Search products..."
                    className="w-full px-4 py-2 rounded-l-md border border-gray-600 focus:outline-none focus:ring-2 focus:ring-blue-400 text-black"
                  />
                  <button className="px-4 py-2 bg-blue-400 text-white rounded-r-md hover:bg-blue-500 transition">
                    <FaSearch />
                  </button>
                </motion.div>
              )}
            </AnimatePresence>
          </div>

          {/* Cart */}
          <div className="relative cursor-pointer">
            <FaShoppingCart className="text-gray-200 hover:text-blue-400 transition text-xl" />
            <span className="absolute -top-2 -right-2 bg-blue-400 text-white text-xs rounded-full px-2">
              {cartItems.length || 0}
            </span>
          </div>

          {/* Login & Sign Up (Desktop) */}
          <div className="hidden md:flex gap-3">
            <button className="px-4 py-2 bg-gray-700 text-white rounded-xl shadow-md hover:bg-gray-600 transition">
              Login
            </button>
            <button className="px-4 py-2 bg-blue-400 text-white rounded-xl shadow-md hover:bg-blue-500 transition">
              Sign Up
            </button>
          </div>

          {/* Mobile Menu Toggle */}
          <button
            className="md:hidden text-gray-200 text-2xl"
            onClick={() => setMenuOpen(!menuOpen)}
          >
            {menuOpen ? <FaTimes /> : <FaBars />}
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      {menuOpen && (
        <div className="md:hidden bg-gray-900 text-gray-200 px-6 py-4 flex flex-col gap-4">
          <a href="#" className="hover:text-blue-400 transition">Home</a>
          <a href="#" className="hover:text-blue-400 transition">Products</a>
          <a href="#" className="hover:text-blue-400 transition">Categories</a>
          <a href="#" className="hover:text-blue-400 transition">About</a>
          <a href="#" className="hover:text-blue-400 transition">Contact</a>

          {/* Mobile Login & Sign Up */}
          <div className="flex gap-3 mt-2">
            <button className="flex-1 px-4 py-2 bg-gray-700 text-white rounded-xl hover:bg-gray-600 transition">
              {LoginPage}
            </button>
            <button className="flex-1 px-4 py-2 bg-blue-400 text-white rounded-xl hover:bg-blue-500 transition">
              {signu}
            </button>
          </div>

          {/* Mobile Search */}
          <div className="flex mt-4">
            <input
              type="text"
              placeholder="Search products..."
              className="w-full px-4 py-2 rounded-l-md border border-gray-600 text-black"
            />
            <button className="px-4 py-2 bg-blue-400 text-white rounded-r-md hover:bg-blue-500 transition">
              <FaSearch />
            </button>
          </div>
        </div>
      )}
    </motion.header>
  );
};

export default Navbar;
